$primary-color: #225173;
$secondary-color: #fdfdfd;
$tertiary-color: #dddddd;


body {
	font-family: 'Montserrat', sans-serif;
	line-height: 2.5em; }
h1, h2, h3, p, li, figure, body {
	margin: 0; }
.hero {
	background: url('img/candlelight.jpg') no-repeat;
	background-size: 100%;
	color: $secondary-color;
	height: 39vw; }
.hero__title {
	display: flex;
	h1 {
		padding-top: 1.25em; }
	figure img {
		height: 100px;
		float: left; } }
.mission_statement {
	background: $primary-color;
	width: 100%;
	min-height: 15em;
	blockquote {
		padding: 5% 0;
		margin: auto;
		margin-top: 0;
		width: 60%;
		color: $secondary-color;
		text-align: center; } }
.pastor {
	background: $secondary-color;
	.pastor__portrait {
		float: left; }
	.pastor__description {
		line-height: 1.75em; } }
.calendar {
	min-height: 500px;
	width: 100%;
	padding-top: 10%;
	background: $tertiary-color; }
.calendar__calendar {
	height: 250px;
	width: 50%;
	background: lightgreen;
	margin: auto; }
footer {
	background: $primary-color;
	color: $secondary-color; }
